/* Reset some basic styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  background-color: #f4f7fa;
  color: #333;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 20px;
  background-color: #eef2f7;
}

h1 {
  font-size: 2rem;
  margin-bottom: 1.5rem;
  color: #4a90e2;
}

.json-input-section {
  width: 100%;
  max-width: 600px;
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

textarea {
  width: 100%;
  height: 150px;
  padding: 10px;
  border: 2px solid #dfe6e9;
  border-radius: 8px;
  font-size: 1rem;
  font-family: 'Roboto', sans-serif;
  resize: none;
  margin-bottom: 10px;
}

textarea:focus {
  outline: none;
  border-color: #4a90e2;
  box-shadow: 0 0 5px rgba(74, 144, 226, 0.5);
}

button {
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: #4a90e2;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #357ABD;
}

.error {
  color: #e74c3c;
  margin-top: 10px;
}

.dropdown-section {
  width: 100%;
  max-width: 600px;
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

select {
  width: 100%;
  height: 150px;
  padding: 10px;
  border: 2px solid #dfe6e9;
  border-radius: 8px;
  font-size: 1rem;
  font-family: 'Roboto', sans-serif;
}

select:focus {
  outline: none;
  border-color: #4a90e2;
  box-shadow: 0 0 5px rgba(74, 144, 226, 0.5);
}

.response-section {
  width: 100%;
  max-width: 600px;
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}

pre {
  text-align: left;
  background: #f4f4f4;
  padding: 15px;
  border-radius: 8px;
  font-size: 1rem;
  line-height: 1.5;
  overflow-x: auto;
}

@media (max-width: 768px) {
  .App {
    padding: 10px;
  }

  .json-input-section,
  .dropdown-section,
  .response-section {
    width: 100%;
    padding: 15px;
  }

  textarea, select {
    font-size: 0.9rem;
    height: 120px;
  }
}

